<template>
<div>
    <v-stepper v-model="el" class="container fluid insert-stepper" >
      <v-stepper-header>
        <v-stepper-step :complete="el > 1" step="1">Importar os dados</v-stepper-step>

        <v-divider></v-divider>

        <v-stepper-step :complete="el > 2" step="2">Confira os Indicadores</v-stepper-step>

        <v-divider></v-divider>

        <v-stepper-step step="3">Criar Vis達o</v-stepper-step>
      </v-stepper-header>

      <v-stepper-items>
        <v-stepper-content step="1">
          <initial-form ref="iniForm"
            :idMacroindicador="idMacroindicador"
            v-on:update:id-macroindicador="idMacroindicador = $event"
          />

          <v-btn
            color="amber darken-4"
            @click="el = sendForm()"
          >
            Continuar
          </v-btn>

          <v-btn flat>Cancel</v-btn>
        </v-stepper-content>

        <v-stepper-content step="2">

          <tree-fields :idMacroindicador="idMacroindicador"/>

          <v-btn
            color="amber darken-4"
            @click="el = 3"
          >
            Continuar
          </v-btn>
          <v-btn flat>Cancel</v-btn>
        </v-stepper-content>
        <v-stepper-content step="3">

          <create-view
            ref="createview"
            :idMacroindicador="idMacroindicador"
          />

          <v-btn color="amber darken-4" @click="salvarVisao()">
            Salvar Vis達o e Finalizar
          </v-btn>
          <v-btn flat>Cancel</v-btn>
        </v-stepper-content>
      </v-stepper-items>
    </v-stepper>
    <v-dialog
      v-model="dialog"
      hide-overlay
      persistent
      width="300"
    >
      <v-card
        color="primary"
        dark
      >
        <v-card-text>
          Please stand by
          <v-progress-linear
            indeterminate
            color="white"
            class="mb-0"
          ></v-progress-linear>
        </v-card-text>
      </v-card>
    </v-dialog>
  </div>
</template>

<script>
import InitialForm from '@/components/Macroindicador/InitialForm'
import TreeFields from '@/components/Macroindicador/TreeFields'
import CreateView from '@/components/Macroindicador/CreateView'

export default {
  components: {
    'initial-form': InitialForm,
    'tree-fields': TreeFields,
    'create-view': CreateView
  },
  data () {
    return {
      el: 0,
      idMacroindicador: '',
      dialog: false
    }
  },
  watch: {
    idMacroindicador () {
      if (this.idMacroindicador.idMacroindicador)
        this.idMacroindicador = this.idMacroindicador.idMacroindicador
    }
  },
  methods: {
    sendForm () {
      this.$refs.iniForm.send()
      return 2
    },
    salvarVisao () {
      this.$refs.createview.send(this.idMacroindicador)
      this.dialog = true
    }
  },
  mounted () {
    this.$store.subscribe((mutation, state) => {
      switch(mutation.type) {
        case 'visao/updateVisao':
          this.$router.push({ path: `/macroindicadores` })
          this.dialog = false
          break;
      }
    })
  }
}

</script>

<style scoped>
  .insert-stepper {
    height: 100%;
  }
</style>
